.joinus-container {
  min-height: calc(100vh - 4.5rem - 6rem);
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  padding: 2rem 2rem;
}

.joinus-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
  gap: 1rem;
}

.joinus-title1 {
  color: rgb(9, 7, 69);
  font-size: 3rem;
  font-weight: 700;
}

.joinus-subcontent {
  color: rgb(9, 7, 69);
  font-size: 1rem;
  font-weight: 400;
  text-align: center;
}

.joinus-subcontent1 {
  display: flex;
  flex-direction: column;
}

/* .joinus-subcontent2 {
    color: rgb(22, 15, 211);
} */

.joinus-socials {
  flex-direction: row;
  display: flex;
  align-items: center;
  gap: 2rem;
}

.joinus-linkedin,
.joinus-instagram,
.joinus-facebook,
.joinus-github,
.joinus-mail {
  height: 6rem;
  transition: all 0.3s ease-in-out;
}

.joinus-linkedin:hover,
.joinus-instagram:hover,
.joinus-facebook:hover,
.joinus-github:hover,
.joinus-mail:hover {
  transform: scale(1.1);
}
